"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  DocumentTextIcon,
  MagnifyingGlassIcon,
  ArrowPathIcon,
  ClockIcon,
  TagIcon,
} from "@heroicons/react/24/outline";

/* =======================
   Types
======================= */

type RiskLevel = "HIGH" | "MEDIUM" | "LOW";

interface ComparisonItem {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: string | null;
}

interface DocumentCard {
  id: string;
  name: string;
  risk: RiskLevel;
  latestVersion: string;
  lastUpdated: string;
  totalComparisons: number;
}

/* =======================
   Config
======================= */

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() ||
  "http://127.0.0.1:8000";

const riskRank: Record<RiskLevel, number> = {
  HIGH: 3,
  MEDIUM: 2,
  LOW: 1,
};

function normalizeRisk(r?: string | null): RiskLevel {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH")) return "HIGH";
  if (x.includes("MEDIUM")) return "MEDIUM";
  return "LOW";
}

function formatDate(iso: string) {
  try {
    return new Date(iso).toLocaleDateString("th-TH", {
      day: "numeric",
      month: "short",
      year: "numeric",
    });
  } catch {
    return iso;
  }
}

/* =======================
   Page
======================= */

export default function DocumentsPage() {
  const [comparisons, setComparisons] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    setLoading(true);
    const res = await fetch(`${API_BASE}/comparisons?limit=300`, {
      cache: "no-store",
    });
    const data = await res.json();
    setComparisons(Array.isArray(data) ? data : []);
    setLoading(false);
  };

  /* =======================
     Build documents
  ======================= */
  const documents = useMemo<DocumentCard[]>(() => {
    const map = new Map<string, DocumentCard>();

    comparisons.forEach((c) => {
      const name = (c.document_name || "Untitled").trim();

      if (!map.has(name)) {
        map.set(name, {
          id: encodeURIComponent(name),
          name,
          risk: normalizeRisk(c.overall_risk_level),
          latestVersion: c.version_new_label || "-",
          lastUpdated: c.created_at,
          totalComparisons: 0,
        });
      }

      const doc = map.get(name)!;
      doc.totalComparisons++;

      if (new Date(c.created_at) > new Date(doc.lastUpdated)) {
        doc.lastUpdated = c.created_at;
        doc.latestVersion = c.version_new_label || doc.latestVersion;
      }

      const r = normalizeRisk(c.overall_risk_level);
      if (riskRank[r] > riskRank[doc.risk]) doc.risk = r;
    });

    return Array.from(map.values());
  }, [comparisons]);

  const filtered = documents.filter((d) =>
    d.name.toLowerCase().includes(search.toLowerCase())
  );

  /* =======================
     UI
  ======================= */

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-6 py-6">
          <h1 className="text-2xl font-bold text-gray-900">
            üìÇ Documents
          </h1>
          <p className="text-gray-600 mt-1">
            ‡πÄ‡∏•‡∏∑‡∏≠‡∏Å‡πÄ‡∏≠‡∏Å‡∏™‡∏≤‡∏£‡πÄ‡∏û‡∏∑‡πà‡∏≠‡∏î‡∏π‡∏õ‡∏£‡∏∞‡∏ß‡∏±‡∏ï‡∏¥‡∏´‡∏£‡∏∑‡∏≠‡πÄ‡∏õ‡∏£‡∏µ‡∏¢‡∏ö‡πÄ‡∏ó‡∏µ‡∏¢‡∏ö‡πÄ‡∏ß‡∏≠‡∏£‡πå‡∏ä‡∏±‡∏ô
          </p>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-6 py-6 space-y-6">
        {/* Search */}
        <div className="flex items-center gap-3">
          <div className="relative flex-1 max-w-md">
            <MagnifyingGlassIcon className="h-5 w-5 text-gray-400 absolute left-3 top-1/2 -translate-y-1/2" />
            <input
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              placeholder="‡∏Ñ‡πâ‡∏ô‡∏´‡∏≤‡πÄ‡∏≠‡∏Å‡∏™‡∏≤‡∏£..."
              className="w-full pl-10 pr-4 py-2.5 border rounded-lg"
            />
          </div>
          <button
            onClick={fetchData}
            className="p-2.5 border rounded-lg hover:bg-gray-100"
          >
            <ArrowPathIcon
              className={`h-5 w-5 ${
                loading ? "animate-spin" : ""
              }`}
            />
          </button>
        </div>

        {/* Grid */}
        {loading && (
          <div className="text-gray-500">‡∏Å‡∏≥‡∏•‡∏±‡∏á‡πÇ‡∏´‡∏•‡∏î...</div>
        )}

        {!loading && filtered.length === 0 && (
          <div className="text-gray-500">‡πÑ‡∏°‡πà‡∏û‡∏ö‡πÄ‡∏≠‡∏Å‡∏™‡∏≤‡∏£</div>
        )}

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-5">
          {filtered.map((doc) => (
            <div
              key={doc.id}
              className="bg-white border rounded-xl p-4 hover:shadow-md transition"
            >
              <div className="flex items-start justify-between">
                <DocumentTextIcon className="h-10 w-10 text-blue-600" />
                <span
                  className={`text-xs font-bold px-2 py-1 rounded-full ${
                    doc.risk === "HIGH"
                      ? "bg-red-100 text-red-700"
                      : doc.risk === "MEDIUM"
                      ? "bg-amber-100 text-amber-700"
                      : "bg-emerald-100 text-emerald-700"
                  }`}
                >
                  {doc.risk}
                </span>
              </div>

              <h3 className="font-semibold text-gray-900 mt-3 line-clamp-2">
                {doc.name}
              </h3>

              <div className="text-sm text-gray-600 mt-2 space-y-1">
                <div className="flex items-center gap-1">
                  <TagIcon className="h-4 w-4" />
                  v{doc.latestVersion}
                </div>
                <div className="flex items-center gap-1">
                  <ClockIcon className="h-4 w-4" />
                  {formatDate(doc.lastUpdated)}
                </div>
                <div>{doc.totalComparisons} comparisons</div>
              </div>

              <div className="flex gap-3 mt-4 text-sm">
                <Link
                  href={`/history?document_name=${encodeURIComponent(
                    doc.name
                  )}`}
                  className="text-blue-600 hover:underline"
                >
                  History
                </Link>
                <Link
                  href={`/compare?document_name=${encodeURIComponent(
                    doc.name
                  )}`}
                  className="text-gray-700 hover:underline"
                >
                  Compare
                </Link>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
